// ËøõË°åÂºÇÂ∏∏Â§ÑÁêÜ
import { HttpResponse } from '@/common/interface'
import store from '@/store/index'
import axios, {
  AxiosInstance,
  AxiosRequestConfig,
  AxiosResponse,
  Canceler
} from 'axios'
import errorHandle from './errorHandle'

class HttpRequest {
  private baseUrl: string
  private pending: Record<string, Canceler>
  constructor (baseUrl: string) {
    this.baseUrl = baseUrl
    this.pending = {}
  }

  // Ëé∑ÂèñÈÖçÁΩÆ
  getInsideConfig () {
    const config = {
      baseURL: this.baseUrl,
      headers: {
        'Content-Type': 'application/json;charset=utf-8'
      },
      timeout: 10000
    }
    return config
  }

  // ËÆæÁΩÆÊã¶Êà™Âô®
  interceptors (instance: AxiosInstance) {
    // Add a request interceptor
    // ÂèëÈÄÅËØ∑Ê±ÇÊã¶Êà™
    instance.interceptors.request.use(
      config => {
        const token = store.state.token
        if (token) {
          config.headers.Authorization = 'Bearer ' + token
        }
        // Do something before request is sent
        console.log('axios ÂèëÂá∫ËØ∑Ê±ÇÊä•Êñá:', config)
        return config
      },
      err => {
        // Do something with request error

        errorHandle(err)
        return Promise.reject(err)
      }
    )

    // Add a response interceptor
    // ÂìçÂ∫îÊã¶Êà™
    instance.interceptors.response.use(
      res => {
        // Any status code that lie within the range of 2xx cause this function to trigger
        // Do something with response data
        if (res.status === 200) {
          return Promise.resolve(res.data)
        } else {
          return Promise.reject(res)
        }
      },
      err => {
        // Any status codes that falls outside the range of 2xx cause this function to trigger
        // Do something with response error
        errorHandle(err)
        return Promise.reject(err)
      }
    )
  }

  // ÂàõÂª∫ÂÆû‰æã
  request (options: AxiosRequestConfig) {
    const instance = axios.create()
    // Object.assign() ÊñπÊ≥ïÁî®‰∫éÂ∞ÜÊâÄÊúâÂèØÊûö‰∏æÂ±ûÊÄßÁöÑÂÄº‰ªé‰∏Ä‰∏™ÊàñÂ§ö‰∏™Ê∫êÂØπË±°ÂàÜÈÖçÂà∞ÁõÆÊ†áÂØπË±°„ÄÇÂÆÉÂ∞ÜËøîÂõûÁõÆÊ†áÂØπË±°„ÄÇ
    const newOptions = Object.assign(this.getInsideConfig(), options)
    this.interceptors(instance)
    // return instance.request(newOptions)
    return instance(newOptions)
  }

  get (
    url: string,
    config?: AxiosRequestConfig
  ): Promise<AxiosResponse> | Promise<HttpResponse> {
    const options = Object.assign(
      {
        method: 'get',
        url: url
      },
      config
    )
    return this.request(options)
  }

  post (
    url: string,
    data?: any,
    config?: AxiosRequestConfig
  ): Promise<AxiosResponse> | Promise<HttpResponse> {
    const options = Object.assign(
      {
        method: 'post',
        url: url,
        data: data
      },
      config
    )
    return this.request(options)
  }
}

export default HttpRequest
