import { MDInfo } from '@/common/interface'
import { createLogger, createStore } from 'vuex'

const debug = process.env.NODE_ENV === 'production'
export default createStore({
  state: {
    title: '',
    token: '',
    mdInfolist: {} as MDInfo,
    content: '' as string,
    contentname: '' as string,
    num: 0
  },
  mutations: {
    setTitle (state, value) {
      state.title = value
    },
    setToken (state, value) {
      if (value === '') return
      state.token = value
      localStorage.setItem('token', value)
    },
    // ËÆæÁΩÆÁî®Êà∑ÁöÑÂü∫Êú¨‰ø°ÊÅØ
    setMDInfo (state, value) {
      if (value === '') return
      state.mdInfolist = value
      // Êú¨Âú∞Â≠òÂÇ®Áî®Êà∑ÁöÑÂü∫Êú¨‰ø°
      localStorage.setItem('mdInfolist', JSON.stringify(value))
    },
    setContentName (state, value: string) {
      state.contentname = value
    },
    setContent (state, value: string) {
      state.content = value
    },
    setMessage (state, value) {
      state.num = value.message ? value.message : 0
    }
  },
  actions: {
    message ({ commit }, msg) {
      commit('setMessage', msg)
    }
  },
  modules: {},
  plugins: !debug ? [createLogger()] : []
})
